.headerContent {
    display: flex;
    flex-direction: column;
    justify-content: center; 
    align-items: center;

}

.titleName {
    color: white;
    font-size: 7rem;
    font-family: 'Lato', sans-serif;
    font-weight: 600
}


.navbar {
    background-color: #3f3f3f;
}

.navbarFlex {
    margin: 0 20%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-content: center;
    height: 100%;
}

.navbarItem {
    color: white;
    flex-grow: 2;
    text-align: center;
    font-size: 1.3em;
    height: 100%;
}

.navbarItem:hover {
    background-color: #2e2e2e;
    cursor: pointer;
}

.navbarTitle {
    margin-top: 7px;
}

.navBuffer {
    flex-grow: 100;
}

a {
    color: black;
}

.page {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

